module.exports = "import React from 'react';\nimport PropTypes from 'prop-types';\nimport {withStyles} from '@material-ui/core/styles';\nimport CircularProgress from '@material-ui/core/CircularProgress';\nimport Paper from '@material-ui/core/Paper';\nimport LinearProgress from '@material-ui/core/LinearProgress';\n\nconst styles = theme => ({\n    root                 : {\n        flexGrow: 1\n    },\n    progress             : {\n        margin: theme.spacing.unit * 2,\n        color : '#00695c'\n    },\n    linearColorPrimary   : {\n        backgroundColor: '#b2dfdb'\n    },\n    linearBarColorPrimary: {\n        backgroundColor: '#00695c'\n    },\n    // Reproduce the Facebook spinners.\n    facebook             : {\n        margin  : theme.spacing.unit * 2,\n        position: 'relative'\n    },\n    facebook1            : {\n        color: '#eef3fd'\n    },\n    facebook2            : {\n        color            : '#6798e5',\n        animationDuration: '550ms',\n        position         : 'absolute',\n        left             : 0\n    }\n});\n\nfunction CustomizedProgress(props)\n{\n    const {classes} = props;\n    return (\n        <Paper className={classes.root}>\n            <CircularProgress className={classes.progress} size={30} thickness={5}/>\n            <LinearProgress\n                classes={{\n                    colorPrimary   : classes.linearColorPrimary,\n                    barColorPrimary: classes.linearBarColorPrimary\n                }}\n            />\n            <div className={classes.facebook}>\n                <CircularProgress\n                    variant=\"determinate\"\n                    value={100}\n                    className={classes.facebook1}\n                    size={24}\n                    thickness={4}\n                />\n                <CircularProgress\n                    variant=\"indeterminate\"\n                    disableShrink\n                    className={classes.facebook2}\n                    size={24}\n                    thickness={4}\n                />\n            </div>\n        </Paper>\n    );\n}\n\nCustomizedProgress.propTypes = {\n    classes: PropTypes.object.isRequired\n};\n\nexport default withStyles(styles)(CustomizedProgress);\n"