module.exports = "import React from 'react';\nimport PropTypes from 'prop-types';\nimport Fade from '@material-ui/core/Fade';\nimport Button from '@material-ui/core/Button';\nimport {withStyles} from '@material-ui/core/styles';\nimport CircularProgress from '@material-ui/core/CircularProgress';\nimport Typography from '@material-ui/core/Typography';\n\nconst styles = theme => ({\n    root       : {\n        display      : 'flex',\n        flexDirection: 'column',\n        alignItems   : 'center'\n    },\n    button     : {\n        margin: theme.spacing.unit * 2\n    },\n    placeholder: {\n        height: 40\n    }\n});\n\nclass DelayingAppearance extends React.Component {\n    state = {\n        loading: false,\n        query  : 'idle'\n    };\n\n    componentWillUnmount()\n    {\n        clearTimeout(this.timer);\n    }\n\n    handleClickLoading = () => {\n        this.setState(state => ({\n            loading: !state.loading\n        }));\n    };\n\n    handleClickQuery = () => {\n        clearTimeout(this.timer);\n\n        if ( this.state.query !== 'idle' )\n        {\n            this.setState({\n                query: 'idle'\n            });\n            return;\n        }\n\n        this.setState({\n            query: 'progress'\n        });\n        this.timer = setTimeout(() => {\n            this.setState({\n                query: 'success'\n            });\n        }, 2e3);\n    };\n\n    render()\n    {\n        const {classes} = this.props;\n        const {loading, query} = this.state;\n\n        return (\n            <div className={classes.root}>\n                <div className={classes.placeholder}>\n                    <Fade\n                        in={loading}\n                        style={{\n                            transitionDelay: loading ? '800ms' : '0ms'\n                        }}\n                        unmountOnExit\n                    >\n                        <CircularProgress/>\n                    </Fade>\n                </div>\n                <Button onClick={this.handleClickLoading} className={classes.button}>\n                    {loading ? 'Stop loading' : 'Loading'}\n                </Button>\n                <div className={classes.placeholder}>\n                    {query === 'success' ? (\n                        <Typography>Success!</Typography>\n                    ) : (\n                        <Fade\n                            in={query === 'progress'}\n                            style={{\n                                transitionDelay: query === 'progress' ? '800ms' : '0ms'\n                            }}\n                            unmountOnExit\n                        >\n                            <CircularProgress/>\n                        </Fade>\n                    )}\n                </div>\n                <Button onClick={this.handleClickQuery} className={classes.button}>\n                    {query !== 'idle' ? 'Reset' : 'Simulate a load'}\n                </Button>\n            </div>\n        );\n    }\n}\n\nDelayingAppearance.propTypes = {\n    classes: PropTypes.object.isRequired\n};\n\nexport default withStyles(styles)(DelayingAppearance);\n"